<br><span class="rvts21"></span><span class="rvts20">Necrofire</span><span class="rvts21"></span>
<br><span class="rvts21"></span><span class="rvts20">Frozen</span><span class="rvts21"></span>
<br><span class="rvts21"></span><span class="rvts20">Shocked</span><span class="rvts21"></span>
<br><span class="rvts21"></span><span class="rvts20">Stunned</span><span class="rvts21"></span>
